import {typeDialogbox} from "../../interfaces/enums";
import {Archivist, Mark, Player, Simna} from "../../characters/chars";
import Sc1 from "./shieldChoice/Sc1";
import Sc2 from "./shieldChoice/Sc2";
import {choiceI, legend, storyI} from "../../interfaces/interfaces";
import StoreStory from "../../mobX/stores/StoreStory";
import toStory from "../../functions/toStory";
import {backgroundsScene3} from "../../../data/backgrounds";

const backgrounds = backgroundsScene3

const Scene3 = toStory((): legend[] => {

    const shieldChoice: choiceI[] = [
        {
            text: '',
            story: Sc1()
        },
        {
            text: '  ',
            story: Sc2()
        }
    ]

    return [
        {
            id: 1,
            background: backgrounds.black,
            speaker: Player.name,
            text: "  .",
        },
        {
            id: 2,
            background: backgrounds.archive_mark,
            text: "    .     -    .",
        },
        {
            id: 2,
            background: backgrounds.archive,
            characters: [Mark.main],
            text: ", ,   ,   .",
        },
        {
            id: 2,
            speaker: Mark.name,
            text: "   !...",
        },
        {
            id: 2,
            speaker: Player.name,
            text: "      ,     ",
            characters: []
        },
        {
            id: 2,
            speaker: "",
            text: "  .",
        },
        {
            id: 2,
            speaker: Simna.name,
            characters: [Archivist.main, Simna.main],
            text: "  ,      .      .",
        },
        {
            id: 2,
            speaker: Archivist.name,
            text: "     !          .",
        },
        {
            id: 2,
            speaker: "",
            text: "    ,   .",
        },
        {
            id: 2,
            speaker: Archivist.name,
            text: " ,        !",
            choice: shieldChoice
        }

    ]
}, backgrounds)

export default Scene3